import React, { useState, useEffect } from "react";
import "./index.css";
import Avatar from "../images/Avatar.jpg";
// import { StaticImage } from "gatsby-plugin-image"

const IndexPage = () => {
  // 设置初始主题为 light 模式
  const [theme, setTheme] = useState("light");
  const [showAboutMe, setShowAboutMe] = useState(false);

  // 根据主题状态更新 body 的类名
  useEffect(() => {
    document.body.className = theme;
  }, [theme]);

  const handleAboutMeClick = (e) => {
    e.preventDefault();
    setShowAboutMe(!showAboutMe); // 切换显示/隐藏 "About me" 内容
  };

  return (
    <main className={`container ${showAboutMe ? "about-me-active" : ""}`}>
      <section className="left">
        <div className="theme-switch">
          <input
            type="radio"
            id="light"
            name="theme"
            value="light"
            checked={theme === "light"}
            onChange={() => setTheme("light")}
          />
          <label htmlFor="light" className="light">
            <span className="label-text">LIGHT</span>
          </label>
          <input
            type="radio"
            id="dark"
            name="theme"
            value="dark"
            checked={theme === "dark"}
            onChange={() => setTheme("dark")}
          />
          <label htmlFor="dark" className="dark">
            <span className="label-text">DARK</span>
          </label>
        </div>
      </section>

      <section className="right">
        <div className={`right-top ${showAboutMe ? "compressed" : ""}`}>
          <div className="name-and-image">
            <div className="name">
              <h1>Shayne Wang</h1>
              <h5>Front-end Engineer</h5>
            </div>
            <div className="image">
              <img src={Avatar} alt="Shayne Wang" />
              {/* <StaticImage src="../images/Avatar.jpg" alt="Shayne Wang" /> */}
            </div>
          </div>
        </div>

        {showAboutMe && (
          <div className="about-me-content">
            <h2>About Me</h2>

            <p>I’m into movies, music, and books.</p>
            <p>
              I love traveling, photography, and diving into the rich cultures
              and quirky traditions of different countries.
            </p>
            <p>
              I’m also fascinated by brain science, philosophy, psychology,
              AI—you name it, if it’s cutting-edge and cool, I’m in!
            </p>
            <p>
              I'm constantly exploring and learning in this vibrant, colorful
              world...
            </p>

            <p>
              And hey, I’m also into front-end coding, so one day, I hope to
              mash up my passions with my skills. Who knows what kind of
              creative sparks could fly when worlds collide!
            </p>
          </div>
        )}

        <div className="divider"></div>
        <div className="right-bottom">
          <nav className="navigation">
            <ul>
              <li>
                <a href="/projects">Projects</a>
              </li>
              <li>
                <a href="/resume" target="_blank">
                  Resume
                </a>
              </li>
              <li>
                <a href="#" onClick={handleAboutMeClick}>
                  About me
                </a>
              </li>
              <li>
                <a href="/contact">Contact</a>
              </li>
            </ul>
          </nav>
          <div className="introduction">
            <p>
              Passionate front-end developer eager to fuse IT withdiverse fields
              to drive innovation and create transformative experiences.
            </p>
          </div>
        </div>
      </section>
    </main>
  );
};

export const Head = () => <title>Home Page</title>;

export default IndexPage;
